import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld';
import RuleInfoRouter from '@/components/Rule/RuleInfoRouter'
import RuleInfoMain from '@/components/Rule/RuleInfoMain'
import RuleInfoBusiness from '@/components/Rule/RuleInfoBusiness'
import RuleInfoFinance from '@/components/Rule/RuleInfoFinance'
import RuleInfoPolicy from '@/components/Rule/RuleInfoPolicy'
import RuleInfoOther from '@/components/Rule/RuleInfoOther'

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: '/static/dist/',
  routes: [
    {
      path: '/helloWorld',
      name: 'HelloWorld',
      component: HelloWorld,
    },
    {
      path: '/',
      name: 'RuleInfo',
      component: RuleInfoRouter,
      children: [
        // {
        //   path: '/',
        //   redirect: 'main',
        // },
        {
          path: '/',
          name: 'RuleInfoMain',
          component: RuleInfoMain,
          meta: {
            title: "主体信息",
            keepAlive: false
          }
        },
        {
          path: 'business',
          name: 'RuleInfoBusiness',
          component: RuleInfoBusiness,
          meta: {
            title: "业务信息"
          }
        }, {
          path: 'finance',
          name: 'RuleInfoFinance',
          component: RuleInfoFinance,
          meta: {
            title: "财务信息"
          }
        }, {
          path: 'policy',
          name: 'RuleInfoPolicy',
          component: RuleInfoPolicy,
          meta: {
            title: "政策法规"
          }
        }, {
          path: 'other',
          name: 'RuleInfoOther',
          component: RuleInfoOther,
          meta: {
            title: "其它信息",
          }
        }
      ]
    }
  ]
})



